// Animate.css
// ============
// Turn specifice animate.css animations on or off

// Fade Enter
$use-fadeIn 			: true;
$use-fadeInUp 			: false;
$use-fadeInUpBig 		: false;
$use-fadeInDown 		: false;
$use-fadeInDownBig 		: false;
$use-fadeInLeft 		: false;
$use-fadeInLeftBig 		: false;
$use-fadeInRight 		: false;
$use-fadeInRightBig 	: false;

// Fade Exit
$use-fadeOut			: true;
$use-fadeOutUp			: false;
$use-fadeOutUpBig   	: false;
$use-fadeOutDown		: false;
$use-fadeOutDownBig 	: false;
$use-fadeOutLeft		: false;
$use-fadeOutLeftBig 	: false;
$use-fadeOutRight		: false;
$use-fadeOutRightBig	: false;

// Bounce Enter
$use-bounceIn			: false;
$use-bounceInUp			: false;
$use-bounceInDown 		: false;
$use-bounceInLeft 		: false;
$use-bounceInRight 		: false;

// Bounce Exit
$use-bounceOut			: false;
$use-bounceOutUp		: false;
$use-bounceOutDown 		: false;
$use-bounceOutLeft 		: false;
$use-bounceOutRight 	: false;

// Rotate Enter
$use-rotateIn 			: false;
$use-rotateInUpLeft 	: false;
$use-rotateInUpRight 	: false;
$use-rotateInDownLeft 	: false;
$use-rotateInDownRight 	: false;

// Rotate Exit
$use-rotateOut 			: false;
$use-rotateOutUpLeft 	: false;
$use-rotateOutUpRight 	: false;
$use-rotateOutDownLeft 	: false;
$use-rotateOutDownRight : false;

// Slide Enter
$use-slideIn			: false;
$use-slideInUp			: true;
$use-slideInDown		: false;
$use-slideInLeft		: true;
$use-slideInRight		: true;

// Slide Exit
$use-slideOut			: false;
$use-slideOutUp			: false;
$use-slideOutDown		: false;
$use-slideOutLeft		: true;
$use-slideOutRight		: true;

// Flippers
$use-flipInX			: false;
$use-flipInY			: true;
$use-flipOutX			: false;
$use-flipOutY			: true;

// Lightspeed
$use-lightSpeedIn 		: false;
$use-lightSpeedOut 		: false;

// Attention Seekers
$use-bounce				: false;
$use-flash				: false;
$use-pulse				: false;
$use-wiggle				: false;
$use-swing				: false;
$use-shake				: false;
$use-tada				: false;
$use-wobble				: false;

// Special
$use-hinge				: false;
$use-rollIn				: true;
$use-rollOut 			: true;

// Zoom In
$use-zoomIn 			: true;
$use-zoomInDown 		: false;
$use-zoomInLeft 		: false;
$use-zoomInRight 		: false;
$use-zoomInUp 			: false;

// Zoom Out
$use-zoomOut			: true;
$use-zoomOutDown		: false;
$use-zoomOutLeft		: false;
$use-zoomOutRight		: false;
$use-zoomOutUp			: false;